.navbar {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    &__log{
        padding-top: 5vh;
        ul{
            display: flex;
            align-items: center;
            li{
                margin-left: 5vw;
                font-size: 14px;
                color: #737373;
                &:last-child {
                    border: 0.75px solid #FAD648;
                    padding: 10px 15px;
                   
                }
            }
        }
    }
    &__menu {
        padding-top: 5vh;
        ul{
            display: flex;
            align-items: center;
            li{
                color: $font-color;
                margin-left: 5vw;
                font-size: 18px;
            }
        }
        // &__start {
        //     border: 0.75px solid $font-color;
        //     padding: 10px 20px;
        // }
    }
}